
body {font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(190, 238, 230) }
 * {box-sizing: border-box;}
 
 form {
   margin:0px auto;
   width:400px;
   }
 
 input, select {
   width: 100%;
   padding: 12px;
   border: 1px solid #ccc;
   border-radius: 4px;
   box-sizing: border-box;
   margin-top: 6px;
   margin-bottom: 16px;
   resize: vertical;
 }
 
 input[type=submit] {
   background-color: #4CAF50;
   color: white;
   padding: 12px 20px;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   margin-top:20px;
 }
 
 input[type=submit]:hover {
   background-color: #45a049;
 }
 
 .container {
   border-radius: 5px;
   padding: 20px;
 }
 
 table {
   border-collapse: collapse;
   width: 100%;
   margin:0px auto;
 }
 
 th, td {
   padding: 15px;
   text-align: left;
 }
 
 th{
   background-color:   rgb(0, 51, 128);
   color: white;
 }
 
 .button {
   background-color: #4CAF50; 
   border-radius: 8px;
   color: white;
   padding: 15px 32px;
   text-align: center;
   text-decoration: none;
   display: inline-block;
   font-size: 16px;
   margin-left:100px;
   margin-top:20px;
 }
 
 .button-tbl {
   border: none;
   text-align: center;
   width:30px;
   height:39px;
   display: inline-block;
   margin-bottom:8px;
   background-color: rgb(190, 238, 230) ;
   margin-left: 20px;
   color:blue;
 
 }
 
 .error{
   color:red;
 }
 
 ul {
   list-style-type: none;
   margin: 0;
   padding: 0;
   overflow: hidden;
   background-color: rgb(6, 6, 10);
   
 }
 
 li {
   float: left;
   display: block;
   color: white;
   text-align: center;
   padding: 14px 16px;
   text-decoration: none;
 }
 
 .text-link {
   text-decoration: none;
   background-color: black;
   color:white
 }
 
 